nav.main {
    padding: 0 30px 30px 0;
}

.main ul {
    list-style-type: none;
}

nav.main a {
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-size: 15px;
    font-weight: bold;
    padding: 10px;
    transition: .2s;
}

nav.main a:hover {
    transform: translateX(20px);
}

nav.main a.active {
    border-left: 10px gray solid;
    transform: translateX(0);
}